.club-card {
  color: $color-midnight-blue;
  position: relative;
  box-sizing: border-box;
  min-height: 246px;
  border: 1px solid $color-snuff;
  border-radius: 24px;
  padding: 40px;
  padding-bottom: 30px;
  background-color: $color-default-white;

  @include vp--1023 {
    padding-bottom: 28px;
  }

  @include vp--767 {
    padding-right: 20px;
  }

  @media (max-width: 371px) {
    padding-left: 15px;
    padding-right: 9px;
  }
}

.club-card--dark {
  min-height: 275px;
  //max-width: 400px;
  width: 100%;
  height: 100%;
  padding: 55px 38px;
  padding-bottom: 30px;
  border: 1px solid $color-perano;
  box-sizing: border-box;

  .club-card__discount {
    background: linear-gradient(90deg, #F185D0 0%, #83B8FB 100%);
  }

  .club-card__name {
    font-size: 23px;
    margin-bottom: 10px;
  }

  .club-card__price-box {
    font-size: 36px;
    line-height: 54px;
    margin-bottom: 29px;
  }

  span {
    //display: flex;
    white-space: nowrap;
  }

  .club-card__link {
    display: block;
    max-width: 100%;
    word-break:  break-all;
    font-size: 12px;
    line-height: 16px;
    padding: 14px 32px;
    //padding-top: 12px;
    border-radius: 100px;
    background-color: $color-rich-blue;
    transition: background-color .3s ease;
  }

  .club-card__link:hover,
  .club-card__link:focus-within {
    background-color: $color-dark-slate-blue;
  }
}

.club-card__discount {
  position: absolute;
  background-color: $color-neon-pink;
  padding: 4px 8px 5px 11px;
  //padding-top: 3px;
  top: 0;
  color: $color-default-white;
  left: 40px;
  max-width: calc(100% - 80px);
  word-break:  break-all;
  border-radius: 0 0 4px 4px;
  font-size: 13px;
  line-height: 17px;

  @include vp--1023 {
    padding: 4px 8px 5px 13px;
  }

  @include vp--767 {
    left: 42px;
    max-width: calc(100% - 84px);
  }
}

.club-card__name {
  font-weight: 500;
  font-size: 28px;
  line-height: 130%;
  margin: 0;
  margin-bottom: 16px;
  padding: 0;

  @include vp--1023 {
    padding-left: 2px;
    margin-bottom: 18px;
  }

  @include vp--767 {
    padding-left: 4px;
  }
}

.club-card__price-box {
  font-weight: 500;
  font-size: 40px;
  line-height: 48px;
  margin-bottom: 16px;
  display: grid;
  grid-template-columns: fit-content(105px) 1fr;
  column-gap: 16px;

  @include vp--1023 {
    padding-left: 2px;
    margin-bottom: 22px;
  }

  @include vp--767 {
    padding-left: 4px;
    margin-bottom: 20px;
  }

  @media (max-width: 371px) {
    column-gap: 10px;
  }
}

.club-card__price {
  display: grid;
  grid-template-columns: fit-content;
  align-items: start;
  align-self: start;
  //row-gap: 2px;

  &--all-price {
    position: relative;
    padding-left: 18px;
    margin-bottom: 16px;

    .club-card__period {
      max-width: 300px;
    }

    &::before {
      background-color: $color-silver;
      position: absolute;
      content: "";
      width: 1px;
      height: 57px;
      top: 12%;
      left: 0;

      @include vp--767 {
        top: 8%;
      }
    }

    @media (max-width: 371px) {
      padding-left: 10px;
    }
  }
}

.club-card__period {
  font-size: 16px;
  line-height: 130%;

  @include vp--1023 {
    line-height: 16px;
  }
}

.club-card__link {
  display: block;
  padding: 10px 43px;
  padding-top: 12px;
  background-color: $color-neon-pink;
  color: $color-default-white;
  width: max-content;
  border-radius: 8px;
  cursor: pointer;

  @include vp--767 {
    padding-left: 45px;
    padding-right: 45px;
  }
}

.club-card__link:hover,
.club-card__link:focus-within {
  background-color: $color-hot-pink;
}