.club-cards {
  @include vp--1023 {
    padding-top: 18px;
  }

  @include vp--767 {
    padding: 0 2px;
    padding-top: 24px;
  }

  @include vp--372 {
    padding: 0;
    padding-top: 24px;
  }
}

.club-cards__title {
  font-weight: 600;
  font-size: 21px;
  line-height: 115%;
  margin: 0;
  padding: 0;
  margin-bottom: 22px;

  @include vp--1023 {
    position: absolute;

    width: 1px;
    height: 1px;
    margin: -1px;
  
    clip: rect(0 0 0 0);
  }
}

.club-cards__wrapper {
  display: grid;
  max-width: 1024px;
  grid-template-columns: 1fr 1fr;
  column-gap: 16px;
  row-gap: 16px;

  @include vp--1023 {
    grid-template-columns: 504px;
    row-gap: 18px;
  }

  @include vp--767 {
    grid-template-columns: 1fr;
    max-width: 504px;
  }
}

.club-cards__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: 1fr;
  row-gap: 16px;
  //padding-left: 2px;

  @include vp--1023 {
    row-gap: 14px;
  }

  @include vp--767 {
    row-gap: 16px;
  }
}

.club-cards__list.club-cards__list--row {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  column-gap: 16px;
  max-width: 100%;
  width: 100%;
  padding-top: 0;

  @include vp--1023 {
    grid-template-columns: 1fr 1fr;
  }

  @include vp--767 {
    grid-template-columns: 1fr;
    padding-top: 8px;
  }
  
  .club-cards__item {
    //width: 32.4%;
    //min-width: 352px;

    @include vp--767 {
      min-width: 100%;
    }
  }

  @include vp--1023 {
    padding-top: 0;
  }
}

