.form {
  margin-bottom: 40px;
  max-width: 504px;

  @include vp--767 {
    margin-bottom: 42px;
  }

  button {
    display: block;
    border: 1px solid $color-rich-blue;
    background-color: $color-rich-blue;
    color: $color-default-white;
    font-size: 12px;
    line-height: 14px;
    cursor: pointer;
    padding: 12px 15px;
    border-radius: 4px;
    margin-left: auto;

    @include vp--1023 {
      padding: 11px 13px;
    }

    @include vp--767 {
      margin-top: 25px;
    }

    &:focus,
    &:hover {
      background-color: $color-dark-state-blue;
      outline: none;
      transition: background-color .3s ease;
    }

    &:active {
      opacity: 0.8;
      transition: background-color .3s ease;
    }

    &:disabled {
      background-color: $color-perano;
      color: $color-default-white;
      border-color: $color-perano;
      transition: background-color .3s ease;
    }
  }
}

.form--enter {
  @include vp--767 {
    padding-top: 2px;

    button {
      width: 100%;
      margin-top: 5px;
      margin-bottom: 24px;
    }
  }
}

.form__title {
  padding-bottom: 22px;

  @include vp--1023 {
    font-size: 21px;
    line-height: 24px;
  }

  @include vp--767 {
    font-size: 21px;
    line-height: 24px;
    padding-bottom: 22px;
  }
}

.form__input {
  display: flex;
  flex-direction: column;
  margin-bottom: 16px;

  label {
    font-size: 13px;
    line-height: 18px;
    margin-bottom: 7px;
    color: $color-default-black;
  }

  input {
    border-radius: 4px;
    border: 1px solid $color-very-light-grey;
    height: 40px;
    padding: 10px;

    &:hover {
      border: 1px solid $color-noble;
    }

    &:focus {
      outline: none;
      border: 1px solid $color-dark-slate-blue;
      //outline: 4px solid $color-alice-blue;
      outline: none;
      transition: background-color .3s ease;
    }

    &:active {
      opacity: 0.8;
    }
  }
}

.form__wrapper {
  display: grid;
  grid-template-columns: 1fr min-content;
  padding-top: 10px;

  @include vp--767 {
    grid-template-columns: 1fr;
    padding-top: 0;
  }
}

.form__links {
  @include vp--767 {
    grid-row-start: 2;
    display: grid;
    grid-template-columns: 1fr;
  }
}

.form__link {
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: $color-rich-blue;
  margin-right: 8px;
  white-space: nowrap;
  font-family: "Montserrat";
  transition: background-color .3s ease;

  @include vp--767 {
    margin-bottom: 16px;
  }
}

.form__link:hover,
.form__link:focus {
  color: $color-dark-state-blue;
  outline: none;
}

.form__link:active {
  color: $color-perano;
}