@mixin retina {
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    @content;
  }
}

@mixin aspect-ratio($ratio) {
  &::after {
    content: "";
    display: block;
    width: 100%;
    padding-bottom: $ratio * 100%;
  }
}

// Desktop first

@mixin vp--1023 {
  @media (max-width: ($vp--1024 - 1)) {
    @content;
  }
}

@mixin vp--767 {
  @media (max-width: ($vp--768 - 1)) {
    @content;
  }
}

@mixin vp--372 {
  @media (max-width: ($vp--372)) {
    @content;
  }
}
